@charset 'utf-8';
// @import 'layout/grid';
@import 'page-public';

.qrcodesmall-box {
	@include box-background-size(720px, 920px, url(../img/1.png) no-repeat);
	position: relative;
	margin: 0 auto;

	.qrcodesmall {
		position: absolute;
		top: auto;
		bottom: 10px;
		transform: translateX(-50%);
	}

	.qrcodesmall-tips {
		position: absolute;
		left: 50%;
		bottom: 60px;
		color: #fff;
		font-size: 16px;
		transform: translateX(-50%);
	}
}

.qrcodesmall-line {
	background: url(../img/line.png) no-repeat;
	background-size: 68px 2px;
}

.qrcode-txt {
	@include box-background-size(413px, 103px, url(../img/txt.png) no-repeat);
	margin: 10px auto;
}

.gift-prompt,
.nowin-prompt {
	color: $white;
}
